/* Styling for the search bar */
.search-bar {
    display: flex;
    justify-content: left;
    align-items: left;
    margin-bottom: 20px;
  }
  
  .search-bar input {
    width: 8%; /* Adjust width as needed */
    margin-right: 10px;
  }
  
  .search-bar button {
    width:8%;
    margin-bottom: 10px; background-color: #007bff; /* Blue color for the button */
    color: white;
    border: none;
    cursor: pointer;
  }
  
  /* Styling for the list of available rides */
  .available-rides {
    display: flex;
    flex-wrap: wrap; /* Display rides side by side */
  }
  
  .available-rides li { /* Two rides per row, adjust as needed */
    margin: 10px;
    padding: 20px;
    border: 1px solid #007bff; /* Sky blue color border */
    list-style-type: none;
  }
  